import { Button, MenuItem, Paper, Stack, TextField, Typography } from '@mui/material';
import { useFormik } from 'formik';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { PreviewImage } from '../components/PreviewImage';
import { addProduct, fetchProductCategories, fetchProducts } from '../redux/slices/productSlice';
import { useNavigate } from 'react-router-dom';
import * as yup from 'yup';

const validateSchema = yup.object().shape({
  title: yup.string().min(3, ' 3 ').required(' '),
  description: yup
    .string()
    .min(3, ' 3 ')
    .max(50, ' 50 ')
    .required(' '),
  price: yup.number().required(' '),
  rating: yup.number().required(' '),
  stock: yup.number().required('  '),
  category: yup.string().required(' '),
});

export const AddProducts = () => {
  const navigate = useNavigate();
  const { productCategories, products } = useSelector((state) => state.product);

  const dispatch = useDispatch();
  const formik = useFormik({
    initialValues: {
      title: '',
      description: '',
      price: '',
      rating: '',
      stock: '',
      category: '',
    },
    validationSchema: validateSchema,
    onSubmit: (values) => {
      const id = Math.floor(Math.random() * Date.now());
      dispatch(addProduct({ ...values, id }));
      navigate('/');
    },
  });

  useEffect(() => {
    dispatch(fetchProductCategories());
  }, []);

  return (
    <>
      <Typography align='center' variant='h5' mb={5} mt={5}>
         
      </Typography>
      <Stack
        sx={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <Paper
          sx={{
            width: '50vw',
          }}
        >
          <Stack component='form' spacing={2} onSubmit={formik.handleSubmit}>
            <TextField
              label=''
              variant='outlined'
              placeholder=''
              fullWidth
              name='title'
              value={formik.values.title}
              onChange={formik.handleChange}
            />
            <Typography color='red' align='center'>
              {formik.touched.title && formik.errors.title}
            </Typography>

            <TextField
              variant='outlined'
              fullWidth
              label=''
              placeholder=''
              name='description'
              value={formik.values.description}
              onChange={formik.handleChange}
            />
            <Typography color='red' align='center'>
              {formik.touched.description && formik.errors.description}
            </Typography>
            <TextField
              variant='outlined'
              fullWidth
              label=''
              placeholder=''
              name='price'
              value={formik.values.price}
              onChange={formik.handleChange}
            />
            <Typography color='red' align='center'>
              {formik.touched.price && formik.errors.price}
            </Typography>

            <TextField
              variant='outlined'
              fullWidth
              label=''
              name='rating'
              placeholder=''
              value={formik.values.rating}
              onChange={formik.handleChange}
            />
            <Typography color='red' align='center'>
              {formik.touched.rating && formik.errors.rating}
            </Typography>
            <TextField
              variant='outlined'
              fullWidth
              label=''
              name='stock'
              placeholder=''
              value={formik.values.stock}
              onChange={formik.handleChange}
            />
            <Typography color='red' align='center'>
              {formik.touched.stock && formik.errors.stock}
            </Typography>
            <TextField
              select
              onChange={formik.handleChange('category')}
              value={formik.values.category}
              label=' '
            >
              {productCategories?.map((category, i) => (
                <MenuItem key={i} value={category}>
                  {category}
                </MenuItem>
              ))}
            </TextField>
            <Typography color='red' align='center'>
              {formik.touched.category && formik.errors.category}
            </Typography>
            <Button type='submit' variant='contained'>
              
            </Button>
          </Stack>
        </Paper>
      </Stack>
    </>
  );
};
